// Write your code here
import {Component} from 'react'
import SuggestionItem from '../SuggestionItem'
import './index.css'

class GoogleSuggestions extends Component {
  state = {
    userInput: '',
  }

  onChangeInput = event => {
    this.setState({userInput: event.target.value})
  }

  onClickStatus = suggestion => {
    this.setState({userInput: suggestion})
  }

  getFilteredList = () => {
    const {userInput} = this.state
    const {suggestionsList} = this.props
    if (userInput.trim() === '') {
      return suggestionsList
    }
    return suggestionsList.filter(eachItem =>
      eachItem.suggestion.toLowerCase().includes(userInput.toLowerCase()),
    )
  }

  render() {
    const {userInput} = this.state
    const filteredList = this.getFilteredList()

    return (
      <div className="cont">
        <img
          src="https://assets.ccbp.in/frontend/react-js/google-logo.png"
          alt="google logo"
          className="google-img"
        />
        <div className="card">
          <div className="search-item">
            <label htmlFor="input">
              <img
                className="search-icon"
                alt="search icon"
                src="https://assets.ccbp.in/frontend/react-js/google-search-icon.png"
              />
            </label>
            <input
              type="search"
              id="input"
              onChange={this.onChangeInput}
              className="input-ele"
              value={userInput}
              placeholder="Search in Google"
            />
          </div>
          <ul className="ul-li">
            {filteredList.map(eachItem => (
              <SuggestionItem
                onClickStatus={this.onClickStatus}
                eachItem={eachItem}
                key={eachItem.id}
              />
            ))}
          </ul>
        </div>
      </div>
    )
  }
}

export default GoogleSuggestions
